@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Pro:wght@200&family=Merriweather+Sans&family=Montserrat&family=Poppins:wght@300;400;500;600&family=Roboto:ital,wght@0,700;1,500&display=swap');
.container{
    display: grid;
    place-items: center;
    height: 100vh;
    width:100%;
    background-color: #f8f8f8;

}
.inputbox_field h1 {
    margin-top: 10px;
    font-family: 'Montserrat', sans-serif;
}
.temp{
    display: flex;
    flex-direction:row;
    gap: 5px;
    align-items: center;
    justify-content: center;
}
.inputbox_field{
    padding:10%;
    text-align:center;
    background-color: white;
    border-radius: 10px;
    width: 350px;
    box-shadow: 5px 10px 18px #888888;
    padding-top: 0;
    padding-bottom: 50px;
    margin: 20px;
}
.inputbox_field button{
    margin-top: 30px;
    text-transform: inherit !important;
}